import React from "react"

import { Link } from "react-router-dom"

import { useTheme } from 'styled-components'
import styled from 'styled-components/macro'
import styles from './header.module.scss'

import Menu from '../../features/components/menu'
import ConnectWalletBtn from '../../features/components/btns/ConnectWalletBtn'
import IconBtn from '../../components/btns/IconBtn'

const HeaderDiv = styled.div`
  width: 100%;
  height: 110px;
  background-color: ${({ theme }) => theme.colors.bgColor};
`

const AdvDiv = styled.div`
  width: 100%;
  height: 50px;
  text-align: center;
  background-color: #5773FF;
`

const AdvImg = styled.img`
  width: 1280px;
  margin:0 auto;
  height: 50px;
  display: block;
`

const Logo = styled.img`
  width: 150px;
  height: 50px;
  float: left;
`

export interface HeaderProps {
  onDownload: any,
  onNotification: any,
  onSettings: any,
  onSelectLang: any,
}

const Header = ({ onDownload, onNotification, onSettings, onSelectLang, }: HeaderProps) => {

  const theme: any = useTheme()

  return (
    <HeaderDiv>
      <AdvDiv><AdvImg src={theme.imgs.advImg}></AdvImg></AdvDiv>
      <div className={styles.menu}>
        <div><Link to="/"><Logo src={theme.imgs.logo} /></Link></div>
        <Menu />
        <div className={styles.cwbtn}>
          <ConnectWalletBtn />
        </div>
        <div className={styles.iconbtns}>
          <IconBtn marginLeft={'48px'} onClick={onDownload} src={theme.imgs.download_img} w={'20px'} h={'20px'} />
          <IconBtn marginLeft={'28px'} onClick={onNotification} src={theme.imgs.notification_img} w={'20px'} h={'20px'} />
          <IconBtn marginLeft={'28px'} onClick={onSettings} src={theme.imgs.settings_img} w={'20px'} h={'20px'} />
          <IconBtn marginLeft={'28px'} onClick={onSelectLang} src={theme.imgs.lang_img} w={'20px'} h={'20px'} />
        </div>
      </div>
    </HeaderDiv>
  );
}

export default Header;
