import styled from 'styled-components/macro'

const FooterDiv = styled.div`
    background-color: #111111;
    text-align: center;
    color: #ffffff;
    height: 60px;
    line-height: 60px;
`

const Footer = () => {

    return (
        <FooterDiv>
            Copyright (c) 2017-{new Date().getFullYear()}. All Rights Reversed by Loopring.
        </FooterDiv>
    )

}

export default Footer
