import React from "react"
import { useDispatch } from "react-redux"

import HeaderUI from "../../../layouts/header"

//TODO add img files for light mode

import { changeTheme } from "../../../store/reducers/settings_reducer"
import i18n from '../../../plugins/i18n'

const Header = () => {

  const dispatch = useDispatch()

  const onDownload = () => {
    console.log('onDownload')
  }

  const onNotification = () => {
    console.log('onNotification')
  }

  const onSettings = () => {
    console.log('onSettings')
    dispatch(changeTheme())
  }

  const onSelectLang = () => {
    console.log('onSelectLang')
    i18n.changeLanguage(i18n.language === 'en_US' ? 'zh_CN' : 'en_US')
  }

  return (
    <HeaderUI onDownload={onDownload} onNotification={onNotification} onSettings={onSettings} onSelectLang={onSelectLang} />
  );
}

export default Header;
