import React, { lazy } from "react"

import {
  BrowserRouter as Router,
  Switch,
  Route,
  useParams,
  useRouteMatch,
} from "react-router-dom"

import styled from 'styled-components/macro'

import Header from "../features/layouts/header"
import Footer from "../layouts/footer"

/*
const QuotePanel = lazy(() => import("../pages/QuotePanel"))
const TradingPanel = lazy(() => import("../pages/TradingPanel"))
const LiquidityPanel = lazy(() => import("../pages/LiquidityPanel"))
const L2WalletPanel = lazy(() => import("../pages/L2WalletPanel"))
const WalletShowCase = lazy(() => import("../pages/demo_page/wallet_showcase"))
*/

import QuotePanel from "../pages/QuotePanel"
import TradingPanel from "../pages/TradingPanel"
import LiquidityPanel from "../pages/LiquidityPanel"
import L2WalletPanel from "../pages/L2WalletPanel"

import WalletShowCase from "../pages/demo_page/wallet_showcase"


const Container = styled.div`
width: 1280px;
margin: 0 auto;
min-height: 600px;
`

class RouterView extends React.Component {
  render() {
    return (
      <Router>
        <Switch>
          <Route exact path="/">
            <Header />
            <Container>
              <QuotePanel />
            </Container>
            <Footer />
          </Route>
          <Route exact path="/trading/quick">
            <Header />
            <Container>
              <TradingPanel />
            </Container>
            <Footer />
          </Route>
          <Route exact path="/trading/pro">
            <Header />
            <Container>
              <TradingPanel />
            </Container>
            <Footer />
          </Route>
          <Route exact path="/liquidity">
            <Header />
            <Container>
              <LiquidityPanel />
            </Container>
            <Footer />
          </Route>
          <Route exact path="/l2wallet">
            <Header />
            <Container>
              <L2WalletPanel />
            </Container>
            <Footer />
          </Route>
          <Route exact path="/demo">
            <Header />
            <Container>
              <WalletShowCase />
            </Container>
            <Footer />
          </Route>
        </Switch>
      </Router>
    );
  }
}

export default RouterView;
