import styled from 'styled-components/macro'

export interface IconBtnProps {
  src?: any
  w?: string
  h?: string
  marginLeft?: string
  onClick: any
}

const BtnImg = styled.img<IconBtnProps>`
  cursor: pointer;
  margin-left: ${({ marginLeft }) => marginLeft ? marginLeft : 0};
`

const IconBtn = ({ onClick, src, w, h, marginLeft }: IconBtnProps) => {
  return (
    <BtnImg marginLeft={marginLeft} width={w} height={h} src={src} onClick={onClick}>
    </BtnImg>
  );
};

export default IconBtn;
