import { Responsive, WidthProvider } from 'react-grid-layout'

import { rowHeight, layouts, breakpoints, cols } from '../../layouts/responsive_layout/trading_panel'

const ResponsiveGridLayout = WidthProvider(Responsive)

const TradingPanel = () => {

  return (
    <ResponsiveGridLayout className="layout" layouts={layouts} rowHeight={rowHeight}
      breakpoints={breakpoints}
      cols={cols}>
      <div key="basic_info">basic_info</div>
      <div key="order_book">order_book</div>
      <div key="trading_chart">trading_chart</div>
      <div key="quote_list">quote_list</div>
      <div key="order_inputs">order_inputs</div>
      <div key="trade_records">trade_records</div>
      <div key="order_records">order_records</div>
    </ResponsiveGridLayout>
  );
}

export default TradingPanel;
