import baseStyled, { ThemedStyledInterface } from 'styled-components'

import advImg from '../assets/images/adv.png'

import logo_dark from '../assets/images/dark/logo.svg'
import logo_light from '../assets/images/dark/logo.svg'

import submenu_down_dark from '../assets/images/dark/submenu_down.svg'
import submenu_down_light from '../assets/images/dark/submenu_down.svg'

import download_dark from '../assets/images/dark/download.svg'
import notification_dark from '../assets/images/dark/notification.svg'
import settings_dark from '../assets/images/dark/settings.svg'
import lang_dark from '../assets/images/dark/lang.svg'

import download_light from '../assets/images/dark/download.svg'
import notification_light from '../assets/images/dark/notification.svg'
import settings_light from '../assets/images/dark/settings.svg'
import lang_light from '../assets/images/dark/lang.svg'

export const darkTheme = {
  all: {
  },
  name: ' dark',
  colors: {
    bgColor: '#111111',
    cwBtnBgColor: '#373739',
    cwBtnColor: '#ffffff',
    menuActived: '#ffffff',
    menuInactived: '#999999',
    subMenuBgColor: '#ffffff',
  },
  imgs: {
    advImg: advImg,
    logo: logo_dark,
    submenu_down: submenu_down_dark,
    download_img: download_dark,
    notification_img: notification_dark,
    settings_img: settings_dark,
    lang_img: lang_dark,
  }
}

export const lightTheme: Theme = {
  all: { ...darkTheme.all },
  name: 'light',
  colors: {
    bgColor: '#777777',
    cwBtnBgColor: '#373739',
    cwBtnColor: '#ffffff',
    menuActived: '#ffffff',
    menuInactived: '#999999',
    subMenuBgColor: '#000000',
  },
  imgs: {
    advImg: advImg,
    logo: logo_light,
    submenu_down: submenu_down_light,
    download_img: download_light,
    notification_img: notification_light,
    settings_img: settings_light,
    lang_img: lang_light,
  }
}

export type Theme = typeof darkTheme
export const styled = baseStyled as ThemedStyledInterface<Theme>
