import { useTranslation } from "react-i18next"
import styled from 'styled-components/macro'

const Button = styled.button`
cursor: pointer;
background: ${({ theme }) => theme.colors.cwBtnBgColor};
color: ${({ theme }) => theme.colors.cwBtnColor};;
border-radius: 8px;
width: 120px;
height: 36px;
line-height: 36px;
text-align: center;

font-family: Roboto-Black;
font-size: 16px;
line-height: 19px;
`

export interface ConnectWalletBtnProps {
  onClick: any,
}

const ConnectWalletBtn = ({ onClick }: ConnectWalletBtnProps) => {

  const { t } = useTranslation()

  return (
    <Button onClick={onClick}>
      {t('common:connect_btn')}
    </Button>
  );
};

export default ConnectWalletBtn;
