import { Link } from 'react-router-dom'
import { useTranslation } from "react-i18next"

import { useTheme } from 'styled-components'
import styled from 'styled-components/macro'
import styles from './menu.module.scss'

import i18n from '../../plugins/i18n'

export interface MenuLiProps {
    children: React.ReactNode
    active: boolean
    width: number
};

const MenuUl = styled.ul`
float: left;
margin-left: 40px;
`

const MenuLi = styled.li<MenuLiProps>`
cursor: pointer;
float: left;
margin-top: 29px;
margin-left: 10px;
padding: 6px 0px;
list-style-type: none;
transition: 200ms;
width: ${({ width }) => width.toString() + 'px'};
text-align: center;

font-family: ${i18n.language === 'zh_CN' ? 'Source Han Sans CN' : 'Roboto'};

color: ${({ active, theme }) => active ? theme.colors.menuActived : theme.colors.menuInactived};

&:hover {
    color:  ${({ theme }) => theme.colors.menuActived};
}
`

export interface MenuLiImgProps {
    isShow: boolean
}

const MenuLiImg = styled.img<MenuLiImgProps>`
margin-top: 35px;
display: ${({ isShow }) => isShow ? 'block' : 'none'};
`

const MenuLiDiv = styled.div`
`

const SubMenuContent = styled.div`
display: none;
z-index: 1;
padding-top: 9px;
`

const SubMenuContentInternal = styled.div`
background-color: ${({ theme }) => theme.colors.subMenuBgColor};
min-width: 250px;
margin-left: 20px;
box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
`

const SubMenuItem = styled.div`
height: 56px;
padding-top: 12px;
padding-left: 10px;
background-color: #1F2126;
&:hover {
    background-color:  #373739;
}

.submenu_circle {
    float: left;
    margin-top: 5px;
    background-color: #ffffff;
    border-radius: 50%;
	width: 8px;
	height: 8px;
}

.submenu_title {
    color: #ffffff;
    margin-left: 17px;
    font-size: 14px;
    line-height: 18px;
}

.submenu_desc {
    padding-left: 17px;
    color: #999999;
    font-size: 12px;
    line-height: 16px;
}

&:hover .submenu_circle {
    background-color: #5773FF;
}

&:hover .submenu_title {
    color: #5773FF;
}

&:hover .submenu_desc {
    text-shadow: 1px 2px 2px #000, 0 -1px 0 #000, 0 1px 0 #000, -1px 0 0 #000;
}

`

export interface SubMenuProps {
    submenus: any
}

const SubMenu = ({ submenus, }: SubMenuProps) => {

    const { t } = useTranslation()

    return (<SubMenuContent className={styles.dropdown_content}>
        <SubMenuContentInternal>
            {
                submenus.map((sub_value: any, sub_index: number) => {
                    return (
                        <Link to={sub_value['link']} key={sub_index}>
                            <SubMenuItem key={sub_index}>
                                <div className='submenu_circle'></div>
                                <div className='submenu_title'>{t(sub_value['title'])}</div>
                                <div className='submenu_desc'>{t(sub_value['desc'])}</div>
                            </SubMenuItem>
                        </Link>
                    )
                })
            }
        </SubMenuContentInternal>
    </SubMenuContent>)
}

export interface MenuProps {
    menu_data: any
    location: any
}

const Menu = ({ menu_data, location }: MenuProps) => {

    const { t } = useTranslation()
    const theme: any = useTheme()

    return (
        <MenuUl>
            {
                menu_data.map((value: any, index: number) => {
                    const isActive = location.pathname === value['link']
                    const hasSubmenu = value['submenus'].length > 0
                    return (
                        <Link to={hasSubmenu ? '' : value['link']} key={index}>
                            <MenuLiDiv className={styles.dropdown}>
                                <>
                                    <MenuLi active={isActive} width={value['width']}>{t(value['title'])}</MenuLi>
                                    <MenuLiImg isShow={hasSubmenu} src={theme.imgs.submenu_down} />
                                </>
                                {
                                    hasSubmenu ? <SubMenu submenus={value['submenus']} /> : <></>
                                }
                            </MenuLiDiv>
                        </Link>
                    )
                })
            }
        </MenuUl>
    );

}

export default Menu
